aside {
  display: flex;
  position: absolute;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  bottom: 40%;
  right: 0;
  left: 0;
  height: auto;
  font-weight: 300;
  margin: auto;
  z-index: 0;
}

@media (min-width: 1250px) {
  aside {
    width: 90%;
    font-size: 40px;
  }
}

@media (max-width: 1250px) {
  aside {
    width: 100%;
    font-size: 30px;
  }
}

.colorPalette {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  z-index: 0;
  transition: all 0.3s ease-in-out;
}

.colorPalette.active {
  background: linear-gradient(-45deg, #132226, #525b56, #a4978e, #be9063);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
}

@import url("https://fonts.googleapis.com/css2?family=Spartan:wght@700&display=swap");

::-moz-selection {
  background: rgba(170, 170, 170, 0.3);
}

::selection {
  background: rgba(170, 170, 170, 0.3);
}

.content {
  user-select: none;
  cursor: default;
  display: flex;
  position: absolute;
  top: 120%;
  right: 27px;
  align-items: center;
  height: 20px;
  overflow: visible;
  font-size: 20px;
  line-height: 40px;
  color: black;
  justify-content: center;
  background-color: transparent;

  &__container {
    font-weight: 600;
    overflow: hidden;
    height: 20px;
    padding: 0 50px;

    &:before {
      content: "[";
      right: 240px;
    }

    &:after {
      content: "]";
      position: absolute;
      right: 25px;
    }

    &:after,
    &:before {
      position: absolute;
      bottom: -9px;
      font-weight: bold;
      color: #525b56;
      font-size: 20px;
      line-height: 40px;

      -webkit-animation-name: opacity;
      -webkit-animation-duration: 2s;
      -webkit-animation-iteration-count: infinite;
      animation-name: opacity;
      animation-duration: 2s;
      animation-iteration-count: infinite;
    }

    &__text {
      position: absolute;
      display: inline;
      right: 147px;
      float: left;
      margin: 0;
      font-size: 17px;
    }

    &__list {
      margin-top: 44px;
      padding: 0;
      text-align: center;
      list-style: none;

      -webkit-animation-name: change;
      -webkit-animation-duration: 25s;
      -webkit-animation-iteration-count: infinite;
